<template>
    <div class="container">
        <div class="capital-record">
            <div class="tabs">
                <span @click="toggleTabs(1)" :class="{acti:tabs==1}">今日</span>
                <span @click="toggleTabs(2)" :class="{acti:tabs==2}">昨天</span>
                <span @click="shaiXuanFun()" :class="{acti:tabs==3}">筛选</span>
            </div>
            <div class="list" v-if="list">
                <ul v-if="list.length > 0" class="mui-scroll">
                    <li v-for="item in list" :key="item.pk">
                        <div class="title">
                            <span class="fl">{{ item.coinOperType | zijin_type }}</span>
                            <span class="fr">{{item.money > 0 ? `+${item.money}` : `${item.money}`}}元</span>
                        </div>
                        <div class="time">
                            <span class="fl">{{item.createTime | timer}}</span>
                            <span class="fr">余额：{{item.leftCoin}}</span>
                        </div>
                    </li>
                </ul>
                <div v-else class="no-data">没有找到符合查询条件的记录</div>
            </div>
            <loadding v-else></loadding>
        </div>
    </div>
</template>

<script>
    import {lastDay, getToday, getTomorrow } from "@/tool/common/time.js";
    import { mapState } from "vuex";
    export default {
        data(){
            return {
                tabs: 1,
                list: false,
            }
        },
        created(){
            const { capial_record_select } = this.$store.state.User
            if(capial_record_select){
                this.tabs = 3
            }
            this.$store.commit('User/SET_USER', this.$route.query)
            this.getNetData()
            if(this.env.app){
                var json = '{"title":"转换记录","showNai":"y","back":"h5"}'
                this.callHandle(json)
            }
        },
        computed:{
            ...mapState('User', {
                capial_record_select: state => state.capial_record_select
            })
        },
        methods: {
            shaiXuanFun(){
                this.tabs =3
                this.$router.push('/capitalRecord1/search')
            },
            toggleTabs(index){
                this.$store.commit('User/SET_FLASE')
                this.list = false
                this.tabs = index
                this.getNetData()
            },
            getNetData(){
                let data = {
                    ...this.baseUser,
                    pageIndex: 1,
                    pageSize: 60,
                    type:'',
                    startTime: getToday(),
                    endTime: getTomorrow(),
                }
                switch (this.tabs) {
                    case 2:
                        data.startTime = lastDay()
                        data.endTime = getToday()
                        break;
                    case 3:
                        data.startTime = this.capial_record_select.startTime
                        data.endTime = this.capial_record_select.endTime
                        data.type = this.capial_record_select.type
                        break;
                    default:
                        break;
                }
                this.$api.user.reqLogUserCoinList(data)
                .then(res=>{
                    console.log(res);
                    this.list = res.logUserCoinList
                    setTimeout(() => {
                        this.mui(".capital-record .list").scroll({
                            scrollY: true, //是否竖向滚动
                            scrollX: false, //是否横向滚动
                            startX: 0, //初始化时滚动至x
                            startY: 0, //初始化时滚动至y
                            indicators: true, //是否显示滚动条
                            deceleration: 0.0006, //阻尼系数,系数越小滑动越灵敏
                            bounce: true //是否启用回弹
                        })
                    }, 200);
                })
            }
        }
    }
</script>

